(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .directive('backNavbar', backNavbar);

  /** @ngInject */
  function backNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/backNavbar/backNavbar.html',
      scope: {
          content: '@',
          removeMargin: '@'
      },
      controller: BackNavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function BackNavbarController($scope, $window) {
      var vm = this;

      vm.back = function() {
        $window.history.back();
      };

      vm.content = $scope.content;
      vm.removeMargin = $scope.removeMargin !== undefined && $scope.removeMargin;
    }
  }

})();

